.imgbox:hover{
  transform: scale(1.01);
  transition: .1s;
  box-shadow: 3px 3px 5px 0 var(--darkest);
  transition: .3s cubic-bezier(.3, 0, 1, 1);
  cursor: pointer;
}
.imgbox_title{
  width: 90%;
  background-color: var(--lighter);
  padding:5%;
  display: flex;
  justify-content: center;
  color:var(--darkest);
  transition: .3s cubic-bezier(.3, 0, 1, 1);
}
.imgbox:hover .imgbox_title{
  background-color: var(--darkest);
  color:var(--lightest);
}
.imgbox_title *{
  margin:0;
}
.imgbox_content{
  width:90%;
  padding:5%;
  display: flex;
  justify-content: center;
  align-items:center;
  flex-direction: column;
}
.imgbox_content{
  text-align: center;
}
.imgbox_content img{
  object-fit:contain;
  width:100%;
}
.imgbox{
  background-color: var(--lightest);
  color: var(--darkest);
  width:80%;
  display:flex;
  flex-direction:column;
  align-items:center;
  text-align: justify;
  margin: .5em;
  transition: .3s cubic-bezier(.3, 0, 1, 1);
}